<template>
    <div>
      <form id="imgFileForm" enctype="multipart/form-data" method="post">
        <label>
          <input style="position:absolute;opacity:0;" type="file" name="file" id="ImgFile"
                 accept="image/gif,image/jpeg,image/x-png" multiple="multiple" @change="imgChange" />
          <img id="showImg" style="width: 160px;height: 90px;margin: 0 auto" src="../../../../static/img/ImgUpload/上传图片.png">
        </label>
      </form>
    </div>
</template>

<script>
    export default {
        name: "PcImgUpload",
        methods:{
          // 图片改变
          imgChange(){
            var reader = new FileReader();
            // 获取file文本框的files值
            // console.log(document.getElementById("ImgFile"))
            reader.readAsDataURL(document.getElementById("ImgFile").files[0]); //发起异步请求
            // 当异步请求加载时，调用以下函数
            reader.onload = function () {
              //读取完成后，数据保存在对象的result属性中（baes64编码）
              // console.log(this.result)
              // 把路径的编码值赋值给显示图片
              document.getElementById("showImg").src = this.result
            }
          }
        }
    }
</script>

<style scoped>
  #imgFileForm{
    margin-top: 50px;
  }
</style>



