<template>
    <div>
      <!--时间轴组件-->
      <div class="ComponentsPage">
        <div class="ComponentsPageTit">
          <h1>PC端基础组件-图片上传</h1>
        </div>
        <div class="ComponentsMain">
          <!--------------------------------------------------------------------------------->
          <p>图片上传改变图片</p>
          <pre v-highlightA>
          <h2 data-clipboard-target="#id_text1" data-clipboard-action="copy" class="id_copy">复制</h2>
          <code class="html" id="id_text1">
      &lt;pc-img-upload>&lt;/pc-img-upload>
          </code>
        </pre>
          <div class="case" style="height:20vh;background: #323437">
            <div style="text-shadow:none;overflow: hidden;text-align: center;justify-content: center;align-items: center;position: relative;padding: 0 5%; color: #eee9dc;box-sizing: border-box">
              <pc-img-upload></pc-img-upload>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    import PcImgUpload from "../../../package/PcImgUpload/src/PcImgUpload";
    export default {
        name: "ComponentsPcImgUpload",
      components: {PcImgUpload}
    }
</script>

<style scoped>
  pre {
    position: relative;
    padding: 0;
    display: table;
    width: 100%;
  }

  .id_copy {
    position: absolute;
    top: 5px;
    font-size: 14px;
    right: 5px;
    cursor: pointer;
  }

  .ComponentsPage {
    width: 100%;
    background: rgba(0, 0, 0, .4);
    box-shadow: 0 1px 20px #2b2824;
    box-sizing: border-box;
    border-radius: 10px;
    color: #ccc;
    text-shadow: 1px 1px 4px #000;
    font-size: 16px;
    padding: 0 0 20px;
    overflow: hidden;
    margin-bottom: 30px;
  }

  .ComponentsPageTit {
    width: 100%;
    height: 50px;
    background: rgba(0, 0, 0, .1);
    box-sizing: border-box;
    padding: 0 20px 0;
  }

  .ComponentsPageTit h1 {
    float: left;
    line-height: 50px;
    font-size: 20px;
  }

  .ComponentsPageTit h2 {
    float: right;
    line-height: 50px;
    font-size: 16px;
    cursor: pointer;
  }

  .ComponentsMain {
    width: 100%;
    box-sizing: border-box;
    padding: 30px 30px 0;
    font-size: 20px;
  }

  .ComponentsMain p {
    font-size: 16px;
    line-height: 36px;
  }

  .case {
    width: 100%;
    margin-top: 50px;
    position: relative;
    height: auto;
    padding: 5px 2%;
    box-sizing: border-box;
    border: 1px solid #ccc;
    /*box-shadow: 0 1px 20px #2b2824;*/

  }

  .case:before {
    content: "效果展示";
    display: block;
    line-height: 25px;
    height: 25px;
    position: absolute;
    padding: 5px 10px;
    top: -37px;
    left: -1px;
    border: 1px solid #ccc;
    font-size: 14px;
  }
</style>



