<template>
  <div>
    <div class="ComponentsPage">
      <div class="ComponentsPageTit">
        <h1>快速上手</h1>
      </div>
      <div class="ComponentsMain">
        <p>我们来写一个简单的小特效吧</p>
        <p>首先，要创建Vue项目</p>
        <pre v-highlightA>
        <h2 data-clipboard-target="#id_text1" data-clipboard-action="copy" class="id_copy">复制</h2>
          <code class="html" id="id_text1">
      vue create hello-world //3.X版本
      vue init webpack hello-world //低版本
          </code>
        </pre>
        <p>如果没有安装脚手架可以先安装Vue脚手架</p>
        <pre v-highlightA>
          <h2 data-clipboard-target="#id_text2" data-clipboard-action="copy" class="id_copy">复制</h2>
          <code class="javascript" id="id_text2">
      npm install -g @vue/cli //3.X版本
      npm install -g @vue/cli-init //低版本
          </code>
        </pre>
        <p>安装成功，可以检测一下是否安装成功</p>
        <pre v-highlightA>
           <h2 data-clipboard-target="#id_text3" data-clipboard-action="copy" class="id_copy">复制</h2>
          <code class="javascript" id="id_text3">
      vue -V
          </code>
        </pre>
        <p>创建一个Vue项目</p>
        <pre v-highlightA>
          <h2 data-clipboard-target="#id_text4" data-clipboard-action="copy" class="id_copy">复制</h2>
          <code class="javascript" id="id_text4">
      vue create hello-world //Vue3.x版本
      vue init webpack hello-world //Vue低版本
          </code>
        </pre>
        <p>安装我们的UI插件</p>
        <pre v-highlightA>
          <h2 data-clipboard-target="#id_text5" data-clipboard-action="copy" class="id_copy">复制</h2>
          <code class="javascript" id="id_text5">
      npm i ardorui
          </code>
        </pre>
        <p>在创建好的Vue项目中的的main.js文件中引入</p>
        <pre v-highlightA>
          <h2 data-clipboard-target="#id_text6" data-clipboard-action="copy" class="id_copy">复制</h2>
          <code class="javascript" id="id_text6">
      //引入ArdorUI组件
      import helloWord from "ardorui"
      //使用组件
      Vue.use(helloWord);
          </code>
        </pre>
        <p>在Vue中插入</p>
        <pre v-highlightA>
          <h2 data-clipboard-target="#id_text7" data-clipboard-action="copy" class="id_copy">复制</h2>
          <code class="html" id="id_text7">
      &lt;!--引入ArdorUI组件-->
      &lt;hello-word :val="'hello'">&lt;/hello-word>
          </code>
        </pre>
        <div class="case">
          <hello-word :val="'hello'"></hello-word>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Clipboard from "clipboard";

  export default {
    name: "ComponentsPage3",
    components: {},
    mounted() {
      var clipboard = new Clipboard(".id_copy");
      clipboard.on("success", function (element) {//复制成功的回调
        console.log("复制成功，复制内容：" + element.text);
      });
    }
  }
</script>

<style scoped>
  pre {
    position: relative;
    padding: 0;
    display: table;
    width: 100%;
  }

  .id_copy {
    position: absolute;
    top: 5px;
    font-size: 14px;
    right: 5px;
    cursor: pointer;
  }

  .ComponentsPage {
    width: 100%;
    background: rgba(0, 0, 0, .4);
    box-shadow: 0 1px 20px #2b2824;
    box-sizing: border-box;
    border-radius: 10px;
    color: #ccc;
    text-shadow: 1px 1px 4px #000;
    font-size: 16px;
    padding: 0 0 20px;
    overflow: hidden;
    margin-bottom: 30px;
  }

  .ComponentsPageTit {
    width: 100%;
    height: 50px;
    background: rgba(0, 0, 0, .1);
    box-sizing: border-box;
    padding: 0 20px 0;
  }

  .ComponentsPageTit h1 {
    float: left;
    line-height: 50px;
    font-size: 20px;
  }

  .ComponentsPageTit h2 {
    float: right;
    line-height: 50px;
    font-size: 16px;
    cursor: pointer;
  }

  .ComponentsMain {
    width: 100%;
    box-sizing: border-box;
    padding: 30px 30px 0;
    font-size: 20px;
  }

  .ComponentsMain p {
    font-size: 16px;
    line-height: 36px;
  }

  .case {
    width: 100%;
    margin-top: 50px;
    position: relative;
    height: auto;
    padding: 5px 2%;
    box-sizing: border-box;
    border: 1px solid #ccc;
    /*box-shadow: 0 1px 20px #2b2824;*/

  }

  .case:before {
    content: "效果展示";
    display: block;
    line-height: 25px;
    height: 25px;
    position: absolute;
    padding: 5px 10px;
    top: -37px;
    left: -1px;
    border: 1px solid #ccc;
    font-size: 14px;
  }
</style>



