<template>
    <div>
        <h1>省市联动</h1>
        <div id="province">
            <span id="provinceSpan" @click="showProvince">请选择</span>
            <ul id="provinceUl">
                <li :key="index" v-for="(a,index) in cityData.城市代码" @click="selectProvince(index)">{{a.省}}</li>
            </ul>
        </div>
        <div id="city">
            <span id="citySpan" @click="showCity">请选择</span>
            <ul id="cityUl">
                <li :key="index" v-for="(b,index) in city" @click="selectCity(index)">{{b.市名}}</li>
            </ul>
        </div>
    </div>
</template>

<script>
    export default {
        name: "PcProvincesAndCity",
        props:["cityData"],
        data(){
            return {
                index1:0,
                index2:0,
                city:[]
            }
        },
        methods:{
            showProvince(){
                document.getElementById("provinceUl").style.display = "block"
                document.getElementById("citySpan").innerHTML = "请选择"
            },
            selectProvince(index){
                document.getElementById("provinceSpan").innerHTML = this.cityData["城市代码"][index]["省"]
                this.city = this.cityData["城市代码"][index]["市"]
                document.getElementById("provinceUl").style.display = "none"
            },
            showCity(){
                document.getElementById("cityUl").style.display = "block"
            },
            selectCity(index){
                document.getElementById("citySpan").innerHTML = document.getElementById("cityUl").children[index].innerHTML
                document.getElementById("cityUl").style.display = "none"
            }
        }
    }
</script>

<style scoped>
    *{
        margin: 0;
        padding: 0;
    }
    h1{
        margin-top: 30px;
    }
    #province{
        width: 200px;
        height: 50px;
        float: left;
        border: 5px solid #04acb7;
        text-align: center;
        line-height: 50px;
        margin: 50px 50px 0px 300px;
        position: relative;
        cursor: pointer;
    }
    #city{
        width: 200px;
        height: 50px;
        float: left;
        border: 5px solid #04acb7;
        text-align: center;
        line-height: 50px;
        margin-top: 50px;
        margin-left: 150px;
        position: relative;
        cursor: pointer;
    }
    ul{
        border: 5px solid black;
        position: absolute;
        left: -5px;
        top: 55px;
        width: 200px;
        max-height: 500px;
        overflow-y: auto;
        display: none;
    }
    ul,li{
        list-style: none;
        text-align: center;
    }
    li:hover{
        background: #f60;
    }

    /* 滚动条效果 */
    ul::-webkit-scrollbar {
        width: 3px;
    }

    ul::-webkit-scrollbar-thumb,
    ul::-webkit-scrollbar-thumb:horizontal {
        border-radius: 6px;
        background: #04acb7;
    }

    ul::-webkit-scrollbar-button {
        display: none;
    }

    ul::-webkit-scrollbar-track,
    ul::-webkit-scrollbar-track-piece {
        background: -webkit-gradient(linear, left);
    }

    ul::-webkit-scrollbar-track-piece {
        background: #212224;
    }

    li:hover {
        background: #6d41dd;
    }

</style>



