    /*box-shadow: 0 1px 20px #2b2824;*/

  }

  .case:before {
    content: "效果展示";
    display: block;
    line-height: 25px;
    height: 25px;
    position: absolute;
    padding: 5px 10px;
    top: -37px;
    left: -1px;
    border: 1px solid #ccc;
    font-size: 14px;
  }
</style>



