<template>
    <div>
      <span id="Showspan" :style="{color:spanColor}"></span>
      <input type="text" value="输入关键字搜索 * " id="showInput" @focus="SearchFocus" @blur="SearchBlur" :style="{color:inputColor,borderBottom:inputBorder,background:backgroundColor}">
    </div>
</template>

<script>
    export default {
        name: "PcSearchBox",
        props:["spanColor","inputColor","inputBorder","inputHightColor","inputHightBorder","backgroundColor"],
        methods:{
          // 搜索 聚焦
          SearchFocus(){
            document.getElementById("Showspan").innerHTML = "输入关键字搜索";
            document.getElementById("showInput").value = "";
            document.getElementById("showInput").style.borderBottom = this.inputHightBorder;
            document.getElementById("showInput").style.color = this.inputHightColor
          },
          // 搜索 失焦
          SearchBlur(){
            document.getElementById("showInput").value = document.getElementById("Showspan").innerHTML;
            document.getElementById("Showspan").innerHTML = "";
            document.getElementById("showInput").style.borderBottom = this.inputBorder;
            document.getElementById("showInput").style.color = this.inputColor
          }
        }
    }
</script>

<style scoped>
  #showInput{
    width: 300px;
    height: 30px;
    outline: none;
    border: none;
    /*background: #323437;*/
    font-size: 16px;
    text-indent: 10px;
    margin-top: 10px;
  }
  #Showspan{
    display: block;
    font-size: 14px;
    margin-right: 180px;
    margin-top: 20px;
  }
</style>



