<template>
    <div>
        <!--说明-->
        <div class="ComponentsPage">
            <div class="ComponentsPageTit">
                <h1>PC端表格&分页组件引用方法</h1>
            </div>
            <div class="ComponentsMain">
                <p>在Vue的main.js中引用</p>
                <pre v-highlightA>
          <h2 data-clipboard-target="#id_text4" data-clipboard-action="copy" class="id_copy">复制</h2>
          <code class="javascript" id="id_text4">
     import PcTable from "ardorui"
     Vue.use(PcTable);
          </code>
        </pre>
            </div>
        </div>
        <!--分页表格组件-->
        <div class="ComponentsPage">
            <div class="ComponentsPageTit">
                <h1>PC端基础组件-表格&分页</h1>
            </div>
            <div class="ComponentsMain">
                <!--------------------------------------------------------------------------------->
                <p>加载动画表格</p>
                <pre v-highlightA>
          <h2 data-clipboard-target="#id_text1" data-clipboard-action="copy" class="id_copy">复制</h2>
          <code class="html" id="id_text1">
      &lt;pc-tables :tableData="tableData">&lt;/pc-tables>
      &lt;!--
        表格 数据
            tableData:[
                    [
                        "水果id",
                        "水果名称",
                        "水果价格",
                        "水果库存",
                        "水果介绍",
                    ],
                    [
                        "01001",
                        "小金煌芒果",
                        "45",
                        "4564",
                        "肉质嫩滑 香味浓郁"
                    ],
                    [
                        "01002",
                        "小鲜橙",
                        "23",
                        "421",
                        "健胃消食 降低固醇"
                    ],
                    [
                        "01003",
                        "草莓",
                        "37",
                        "856",
                        "水果皇后 香甜可口"
                    ]
                ]
      -->
          </code>
        </pre>
                <div class="case" style="background: #323437">
                    <div style="text-shadow:none;overflow: hidden;text-align: center;justify-content: center;align-items: center;position: relative;padding: 0 5%; color: #eee9dc;box-sizing: border-box">
                        <pc-tables :tableData="tableData"></pc-tables>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import PcTables from "../../../package/PcTables/src/PcTables";
    export default {
        name: "ComponentsPcTable",
        components: {PcTables},
        data(){
            return {
                tableData:[
                    [
                        "水果id",
                        "水果名称",
                        "水果价格",
                        "水果库存",
                        "水果介绍",
                    ],
                    [
                        "01001",
                        "小金煌芒果",
                        "45",
                        "4564",
                        "肉质嫩滑 香味浓郁"
                    ],
                    [
                        "01002",
                        "小鲜橙",
                        "23",
                        "421",
                        "健胃消食 降低固醇"
                    ],
                    [
                        "01003",
                        "草莓",
                        "37",
                        "856",
                        "水果皇后 香甜可口"
                    ]
                ]
            }
        }
    }
</script>

<style scoped>
    pre {
        position: relative;
        padding: 0;
        display: table;
        width: 100%;
    }

    .id_copy {
        position: absolute;
        top: 5px;
        font-size: 14px;
        right: 5px;
        cursor: pointer;
    }

    .ComponentsPage {
        width: 100%;
        background: rgba(0, 0, 0, .4);
        box-shadow: 0 1px 20px #2b2824;
        box-sizing: border-box;
        border-radius: 10px;
        color: #ccc;
        text-shadow: 1px 1px 4px #000;
        font-size: 16px;
        padding: 0 0 20px;
        overflow: hidden;
        margin-bottom: 30px;
    }

    .ComponentsPageTit {
        width: 100%;
        height: 50px;
        background: rgba(0, 0, 0, .1);
        box-sizing: border-box;
        padding: 0 20px 0;
    }

    .ComponentsPageTit h1 {
        float: left;
        line-height: 50px;
        font-size: 20px;
    }

    .ComponentsPageTit h2 {
        float: right;
        line-height: 50px;
        font-size: 16px;
        cursor: pointer;
    }

    .ComponentsMain {
        width: 100%;
        box-sizing: border-box;
        padding: 30px 30px 0;
        font-size: 20px;
    }

    .ComponentsMain p {
        font-size: 16px;
        line-height: 36px;
    }

    .case {
        width: 100%;
        margin-top: 50px;
        position: relative;
        height: auto;
        padding: 5px 2%;
        box-sizing: border-box;
        border: 1px solid #ccc;
        /*box-shadow: 0 1px 20px #2b2824;*/

    }

    .case:before {
        content: "效果展示";
        display: block;
        line-height: 25px;
        height: 25px;
        position: absolute;
        padding: 5px 10px;
        top: -37px;
        left: -1px;
        border: 1px solid #ccc;
        font-size: 14px;
    }
</style>


