<template>
  <div class="z-example-main">
    <router-link tag="div" to="/" style="cursor: pointer">
      <example-header></example-header>
    </router-link>
    <div class="exampleNav">
      <router-link to="/Example">PC端</router-link>
      <router-link to="/Example/demo2">移动端</router-link>
      <router-link to="/Example/demo3">大数据</router-link>
      <!-- <router-link to="/Example/demo4">JQuery</router-link> -->
    </div>
    <router-view class="ExampleDemo"></router-view>
  </div>
</template>

<script>
  import ExampleHeader from "../components/Example/ExampleHeader";

  export default {
    name: "Example",
    components: {ExampleHeader}
  }
</script>

<style scoped>
  .z-example-main {
    width: 100%;
  }

  .exampleNav {
    width: 100%;
    height: 50px;
    border-top: 1px solid skyblue;
    border-bottom: 1px solid skyblue;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .exampleNav a {
    display: block;
    padding: 0 10px;
    border-left: skyblue 1px solid;
    border-right: skyblue 1px solid;
    margin: 0 20px;
    color: #000;
    transition: all .5s;
  }

  .exampleNav a:hover {
    color: skyblue;
  }

  .ExampleDemo {
    margin-top: 30px;
  }
</style>



