import Vue from 'vue'
import Router from 'vue-router'
import Index from "../pages/Index";
import Example from "../pages/Example";
import ExampleDemo1 from "../components/Example/ExampleDemo1";
import ExampleDemo2 from "../components/Example/ExampleDemo2";
import ExampleDemo3 from "../components/Example/ExampleDemo3";
import ExampleDemo4 from "../components/Example/ExampleDemo4";
import Components from "../pages/Components";
import ComponentsPage1 from "../components/Components/tabs/ComponentsPage1";
import ComponentsUpdate from "../components/Components/tabs/ComponentsUpdate";
import ComponentsPage2 from "../components/Components/tabs/ComponentsPage2";
import ComponentsPage3 from "../components/Components/tabs/ComponentsPage3";
import ComponentsPcBasics from "../components/Components/tabs/ComponentsPcBasics";
import ComponentsPcHeader from "../components/Components/tabs/ComponentsPcHeader";
import ComponentsPcTimeline from "../components/Components/tabs/ComponentsPcTimeline";
import ComponentsPcLogin from "../components/Components/tabs/ComponentsPcLogin";
import ComponentsMbShopcart from "../components/Components/tabs/ComponentsMbShopcart";
import ComponentsMbFind from "../components/Components/tabs/ComponentsMbFind";
import ComponentsMbRegister from "../components/Components/tabs/ComponentsMbRegister";
import ComponentsMbLogin from "../components/Components/tabs/ComponentsMbLogin";
import ComponentsPcTable from "../components/Components/tabs/ComponentsPcTable";
import ComponentsPcTabContor from "../components/Components/tabs/ComponentsPcTabContor";
import ComponentsProvincesAndCity from "../components/Components/tabs/ComponentsProvincesAndCity";
import ComponentsPcImgUpload from "../components/Components/tabs/ComponentsPcImgUpload";
import ComponentsPcSearchBox from "../components/Components/tabs/ComponentsPcSearchBox";
import ComponentsPcNumTotal from "../components/Components/tabs/ComponentsPcNumTotal";

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/Example',
      component: Example,
      children: [
        {
          path: '',
          name: 'ExampleDemo1',
          component: ExampleDemo1
        },
        {
          path: 'demo2',
          name: 'ExampleDemo2',
          component: ExampleDemo2
        },
        {
          path: 'demo3',
          name: 'ExampleDemo3',
          component: ExampleDemo3
        },
        {
          path: 'demo4',
          name: 'ExampleDemo4',
          component: ExampleDemo4
        },
      ]
    },
    {
      path: '/Components',
      name: 'Components',
      component: Components,
      children: [
        {
          path: '',
          name: 'ComponentsPage2',
          component: ComponentsPage2
        },
        {
          path: 'page1',
          name: 'ComponentsPage1',
          component: ComponentsPage1
        },
        {
          path: 'update',
          name: 'ComponentsUpdate',
          component: ComponentsUpdate
        },
        {
          path: 'page3',
          name: 'ComponentsPage3',
          component: ComponentsPage3
        },
        {
          path: 'ComponentsPcBasics',
          name: 'ComponentsPcBasics',
          component: ComponentsPcBasics
        },
        {
          path: 'ComponentsPcHeader',
          name: 'ComponentsPcHeader',
          component: ComponentsPcHeader
        },
        {
          path: 'ComponentsPcTimeline',
          name: 'ComponentsPcTimeline',
          component: ComponentsPcTimeline
        },
        {
          path: 'ComponentsPcTable',
          name: 'ComponentsPcTable',
          component: ComponentsPcTable
        },
        {
          path: 'ComponentsPcTabContor',
          name: 'ComponentsPcTabContor',
          component: ComponentsPcTabContor
        },
        {
          path: 'ComponentsProvincesAndCity',
          name: 'ComponentsProvincesAndCity',
          component: ComponentsProvincesAndCity
        },
        {
          path: 'ComponentsPcLogin',
          name: 'ComponentsPcLogin',
          component: ComponentsPcLogin
        },
        {
          path: 'ComponentsMbShopcart',
          name: 'ComponentsMbShopcart',
          component: ComponentsMbShopcart
        },
        {
          path: 'ComponentsMbFind',
          name: 'ComponentsMbFind',
          component: ComponentsMbFind
        },
        {
          path: 'ComponentsMbRegister',
          name: 'ComponentsMbRegister',
          component: ComponentsMbRegister
        },
        {
          path: 'ComponentsMbLogin',
          name: 'ComponentsMbLogin',
          component: ComponentsMbLogin
        },
        {
          path: 'ComponentsPcImgUpload',
          name: 'ComponentsPcImgUpload',
          component: ComponentsPcImgUpload
        },
        {
          path: 'ComponentsPcSearchBox',
          name: 'ComponentsPcSearchBox',
          component: ComponentsPcSearchBox
        },
        {
          path: 'ComponentsPcNumTotal',
          name: 'ComponentsPcNumTotal',
          component: ComponentsPcNumTotal
        }
      ]
    },
  ]
})



