<template>
  <div>
    <div class="ComponentsUpdate" v-for="(c,i) in dataRouterView" :key="i" :data="c">
      <div class="ComponentsUpdateTit">
        <h1>版本:{{c.version}}</h1>
        <h2>{{c.date}}</h2>
      </div>
      <div class="ComponentsMain">
        <ul>
          <li v-for="(v,i) in c.updateLis" :key="i" :date="v">
            <svg class="icon ComponentsMainIcon" aria-hidden="true">
              <use xlink:href="#icon-AppleUSB"></use>
            </svg>
            {{v}}
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: ["dataRouterView"],
    name: "ComponentsUpdate"
  }
</script>

<style scoped>
  .ComponentsUpdate {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .4);
    box-shadow: 0 1px 20px #2b2824;
    box-sizing: border-box;
    border-radius: 10px;
    color: #ccc;
    text-shadow: 1px 1px 4px #000;
    font-size: 16px;
    padding: 0 0 20px;
    overflow: hidden;
    margin-bottom: 30px;
  }

  .ComponentsUpdateTit {
    width: 100%;
    height: 50px;
    background: rgba(0, 0, 0, .1);
    box-sizing: border-box;
    padding: 0 20px 0;
  }

  .ComponentsUpdateTit h1 {
    float: left;
    line-height: 50px;
    font-size: 20px;
  }

  .ComponentsUpdateTit h2 {
    float: right;
    line-height: 50px;
    font-size: 16px;
  }

  .ComponentsMain {
    width: 100%;
    box-sizing: border-box;
    padding: 30px 30px 0;
    font-size: 20px;
  }

  .ComponentsMainIcon {
    width: 20px;
    height: 20px;
    margin-right: 10px;
  }
</style>



