        <div class="ComponentsPage">
            <div class="ComponentsPageTit">
                <h1>PC端数量统计组件引用方法</h1>
            </div>
            <div class="ComponentsMain">
                <p>在Vue的main.js中引用</p>
                <pre v-highlightA>
          <h2 data-clipboard-target="#id_text4" data-clipboard-action="copy" class="id_copy">复制</h2>
          <code class="javascript" id="id_text4">
     import PcNumTotal from "ardorui"
     Vue.use(PcNumTotal);
          </code>
        </pre>
            </div>
        </div>
        <!--统计动画-->
        <div class="ComponentsPage">
            <div class="ComponentsPageTit">
                <h1>PC端基础组件-数量统计</h1>
            </div>
            <div class="ComponentsMain">
                <!--------------------------------------------------------------------------------->
                <p>统计动画1</p>
                <pre v-highlightA>
          <h2 data-clipboard-target="#id_text1" data-clipboard-action="copy" class="id_copy">复制</h2>
          <code class="html" id="id_text1">
      &lt;pc-num-total1>&lt;/pc-num-total1>
      &lt;!--
        推荐使用animate

        $($("#count").find("span")[i]).animate({
            "backgroundPositionY":positionY
        },"slow","linear");
      -->
          </code>
        </pre>
                <div class="case" style="background: #323437">
                    <div style="text-shadow:none;overflow: hidden;text-align: center;justify-content: center;align-items: center;position: relative;padding: 0 5%; color: #eee9dc;box-sizing: border-box">
                        <pc-num-total></pc-num-total>
                    </div>
                </div>
            </div>
        </div>
        <!--统计动画2-->
        <div class="ComponentsPage">
            <div class="ComponentsPageTit">
                <h1>PC端基础组件-数量统计</h1>
            </div>
            <div class="ComponentsMain">
                <!--------------------------------------------------------------------------------->
                <p>统计动画2</p>
                <pre v-highlightA>
          <h2 data-clipboard-target="#id_text2" data-clipboard-action="copy" class="id_copy">复制</h2>
          <code class="html" id="id_text2">
      &lt;pc-num-total2>&lt;/pc-num-total2>
          </code>
        </pre>
                <div class="case" style="background: #323437">
                    <div style="text-shadow:none;overflow: hidden;text-align: center;justify-content: center;align-items: center;position: relative;padding: 0 5%; color: #eee9dc;box-sizing: border-box">
                        <pc-num-total2></pc-num-total2>
                    </div>
                </div>
            </div>
        </div>
        <!--统计动画3-->
        <div class="ComponentsPage">
            <div class="ComponentsPageTit">
                <h1>PC端基础组件-数量统计</h1>
            </div>
            <div class="ComponentsMain">
                <!--------------------------------------------------------------------------------->
                <p>统计动画3</p>
                <pre v-highlightA>
          <h2 data-clipboard-target="#id_text3" data-clipboard-action="copy" class="id_copy">复制</h2>
          <code class="html" id="id_text3">
      &lt;pc-num-total3>&lt;/pc-num-total3>
          </code>
        </pre>
                <div class="case" style="background: #323437">
                    <div style="text-shadow:none;overflow: hidden;text-align: center;justify-content: center;align-items: center;position: relative;padding: 0 5%; color: #eee9dc;box-sizing: border-box">
                        <pc-num-total3></pc-num-total3>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import PcNumTotal from "../../../package/PcNumTotal/src/PcNumTotal";
    import PcNumTotal2 from "../../../package/PcNumTotal/src/PcNumTotal2";
    import PcNumTotal3 from "../../../package/PcNumTotal/src/PcNumTotal3";
    export default {
        name: "ComponentsPcNumTotal",
        components: {PcNumTotal3, PcNumTotal2, PcNumTotal}
    }
</script>

<style scoped>
    pre {
        position: relative;
        padding: 0;
        display: table;
        width: 100%;
    }

    .id_copy {
        position: absolute;
        top: 5px;
        font-size: 14px;
        right: 5px;
        cursor: pointer;
    }

    .ComponentsPage {
        width: 100%;
        background: rgba(0, 0, 0, .4);
        box-shadow: 0 1px 20px #2b2824;
        box-sizing: border-box;
        border-radius: 10px;
        color: #ccc;
        text-shadow: 1px 1px 4px #000;
        font-size: 16px;
        padding: 0 0 20px;
        overflow: hidden;
        margin-bottom: 30px;
    }

    .ComponentsPageTit {
        width: 100%;
        height: 50px;
        background: rgba(0, 0, 0, .1);
        box-sizing: border-box;
        padding: 0 20px 0;
    }

    .ComponentsPageTit h1 {
        float: left;
        line-height: 50px;
        font-size: 20px;
    }

    .ComponentsPageTit h2 {
        float: right;
        line-height: 50px;
        font-size: 16px;
        cursor: pointer;
    }

    .ComponentsMain {
        width: 100%;
        box-sizing: border-box;
        padding: 30px 30px 0;
        font-size: 20px;
    }

    .ComponentsMain p {
        font-size: 16px;
        line-height: 36px;
    }

    .case {
        width: 100%;
        margin-top: 50px;
        position: relative;
        height: auto;
        padding: 5px 2%;
        box-sizing: border-box;
        border: 1px solid #ccc;
        /*box-shadow: 0 1px 20px #2b2824;*/

    }

    .case:before {
        content: "效果展示";
        display: block;
        line-height: 25px;
        height: 25px;
        position: absolute;
        padding: 5px 10px;
        top: -37px;
        left: -1px;
        border: 1px solid #ccc;
        font-size: 14px;
    }
</style>


