<template>
  <div>
    <div class="ComponentsPage">
      <div class="ComponentsPageTit">
        <h1>npm安装</h1>
      </div>
      <div class="ComponentsMain">
        <p>通过npm下载
        </p>
        <pre v-highlightA>
        <h2 data-clipboard-target="#id_text1" data-clipboard-action="copy" class="id_copy">复制</h2>
          <code class="javascript" id="id_text1">
      npm i ardorui
          </code>
        </pre>
      </div>
    </div>
    <div class="ComponentsPage">
      <div class="ComponentsPageTit">
        <h1>Git安装</h1>
      </div>
      <div class="ComponentsMain">
        <p>通过<a href="https://github.com/zhongjiawei0606/ArdorUI" target="_blank">GitHub</a>下载或者<a href="/"
                                                                                                   target="_blank">点我</a>下载
        </p>
        <pre v-highlightA>
        <h2 data-clipboard-target="#id_text2" data-clipboard-action="copy" class="id_copy">复制</h2>
          <code class="javascript" id="id_text2">
      clone git@github.com:zhongjiawei0606/ArdorUI.git //GitHub地址
          </code>
        </pre>
      </div>

    </div>
    <div class="ComponentsPage">
      <div class="ComponentsPageTit">
        <h1>使用</h1>
      </div>
      <div class="ComponentsMain">
        <p>在Vue的main.js文件中引入</p>
        <pre v-highlightA>
        <h2 data-clipboard-target="#id_text3" data-clipboard-action="copy" class="id_copy">复制</h2>
          <code class="javascript" id="id_text3">
      //引入ArdorUI组件
      import [组件名] from "ardorui";
      //使用组件
      Vue.use([组件名]);
          </code>
        </pre>
      </div>

    </div>
  </div>
</template>

<script>
  import Clipboard from "clipboard";

  export default {
    name: "ComponentsPage2",
    components: {},
    mounted() {
      var clipboard = new Clipboard(".id_copy");
      clipboard.on("success", function (element) {//复制成功的回调
        console.log("复制成功，复制内容：" + element.text);
      });
    }
  }
</script>

<style scoped>
  pre {
    position: relative;
    padding: 0;
    display: table;
    width: 100%;
  }

  .id_copy {
    position: absolute;
    top: 5px;
    font-size: 14px;
    right: 5px;
    cursor: pointer;
  }

  .ComponentsPage {
    width: 100%;
    background: rgba(0, 0, 0, .4);
    box-shadow: 0 1px 20px #2b2824;
    box-sizing: border-box;
    border-radius: 10px;
    color: #ccc;
    text-shadow: 1px 1px 4px #000;
    font-size: 16px;
    padding: 0 0 20px;
    overflow: hidden;
    margin-bottom: 30px;
  }

  .ComponentsPageTit {
    width: 100%;
    height: 50px;
    background: rgba(0, 0, 0, .1);
    box-sizing: border-box;
    padding: 0 20px 0;
  }

  .ComponentsPageTit h1 {
    float: left;
    line-height: 50px;
    font-size: 20px;
  }

  .ComponentsPageTit h2 {
    float: right;
    line-height: 50px;
    font-size: 16px;
    cursor: pointer;
  }

  .ComponentsMain {
    width: 100%;
    box-sizing: border-box;
    padding: 30px 30px 0;
    font-size: 20px;
  }

  .ComponentsMain p {
    font-size: 16px;
    line-height: 36px;
  }

  .case {
    width: 100%;
    margin-top: 50px;
    position: relative;
    height: auto;
    padding: 5px 2%;
    box-sizing: border-box;
    border: 1px solid #ccc;
    /*box-shadow: 0 1px 20px #2b2824;*/

  }

  .case:before {
    content: "效果展示";
    display: block;
    line-height: 25px;
    height: 25px;
    position: absolute;
    padding: 5px 10px;
    top: -37px;
    left: -1px;
    border: 1px solid #ccc;
    font-size: 14px;
  }
</style>



