<template>
  <div class="z-components-menu" dir="rtl">
    <router-link tag="h1" to="/Components/update" class="z-components-h1">更新日志</router-link>
    <router-link tag="h1" to="/Components/page1" class="z-components-h1">ArdorUI for React</router-link>
    <router-link tag="h1" to="/Components/page1" class="z-components-h1">ArdorUI for Angular</router-link>
    <div class="z-com-menu" v-for="(c,i) in data" :key="i" :data="c">
      <h1 class="z-components-h1">{{c.name}}</h1>
      <ul class="z-components-ul">
        <router-link tag="li" :to="b.url" class="z-components-li" v-for="(b,i) in c.ul" :key="i" :data="b">{{b.name}}
        </router-link>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    props: ["data"],
    name: "ComponentsMenu"
  }
</script>

<style scoped>
  .z-components-menu {
    width: 100%;
    height: auto;
    flex: 1;
    overflow-x: hidden;
    overflow-y: auto;
    box-sizing: border-box;
    padding-left: 20px;
    padding-bottom: 50px;
  }

  /* 设置滚动条的样式 */
  .z-components-menu::-webkit-scrollbar {
    width: 5px;
  }

  /* 滚动槽 */
  .z-components-menu::-webkit-scrollbar-track {
    /* -webkit-box-shadow: inset 0 0 6px  rgba(40, 42, 44, 1);
    border-radius: 10px; */
  }

  /* 滚动条滑块 */
  .z-components-menu::-webkit-scrollbar-thumb {
    border-radius: 10px;
    /* background: rgba(204, 204, 204, 1); */
    /* -webkit-box-shadow: inset 0 0 6px rgba(29, 211, 211, 1); */
    /* background:gray; */
  }

  .z-components-menu::-webkit-scrollbar-thumb:window-inactive {
    /* background: rgba(204, 204, 204, 1); */
    /* background:gray; */
  }

  .z-components-h1 {
    color: #ccc;
    text-shadow: 1px 1px 4px #000;
    font-size: 16px;
    margin-bottom: 5px;
    text-align: left;
    cursor: pointer;
  }

  .z-com-menu {
    margin-bottom: 10px;
  }

  .z-components-ul {
    margin-left: 20px;
    box-sizing: border-box;
  }

  .z-components-li {
    color: #ccc;
    text-shadow: 1px 1px 4px #000;
    font-size: 16px;
    padding: 5px;
    border-left: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    text-align: left;
    cursor: pointer;
  }
 
</style>



