<template>
  <div class="card" :style="{backgroundImage:picUrl}">
    <div class="img">
      <span :style="{backgroundImage:picUrl}"></span>
      <span :style="{backgroundImage:picUrl}"></span>
      <span :style="{backgroundImage:picUrl}"></span>
      <span :style="{backgroundImage:picUrl}"></span>
    </div>
    <div class="content">
      <h2>{{data.name}}</h2>
      <h3><i class="iconfont icon-jieshao"></i>{{data.identity}}</h3>
      <h4><i class="iconfont icon-weizhi"></i>{{data.site}}</h4>
      <h4><i class="iconfont icon-youxiang"></i>{{data.email}}</h4>
      <h4><i class="iconfont icon-weixin"></i>{{data.wechat}}</h4>
      <h4><i class="iconfont icon-phone"></i>{{data.phone}}</h4>
      <h4><i class="iconfont icon-github"></i>{{data.github}}</h4>
    </div>
  </div>
</template>

<script>
  export default {
    props: ["data"],
    name: "IndexPagesPicinfo",
    computed: {
      picUrl() {
        return 'url(' + this.data.pic + ')';
      }
    }
  }
</script>

<style scoped>
  .card {
    border-radius: 16px;
    width: 250px;
    height: 250px;
    /*background: url(this.data.pic);*/
    position: relative;
    overflow: hidden;
    box-shadow: 0 5px 50px rgba(0, 0, 0, 0.85);
    flex-shrink: 0;
  }

  .card:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    background-color: rgba(0, 0, 0, 0.92);
  }

  .card .img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
  }

  .card .img span {
    width: 25%;
    height: 100%;
    /*background: url(this.data.pic);*/
    transition: 0.5s;
  }

  .card .img span:nth-child(1) {
    background-position: 0;
    transition-delay: 0;
  }

  .card .img span:nth-child(2) {
    background-position: 33.33%;
    transition-delay: 0.1s;
  }

  .card .img span:nth-child(3) {
    background-position: 66.66%;
    transition-delay: 0.2s;
  }

  .card .img span:nth-child(4) {
    background-position: 100%;
    transition-delay: 0.3s;
  }

  .card:hover .img > span {
    transform: translateY(-100%);
  }

  .card:hover .content {
    transform: translateY(0%);
    transition: 1s;
    transition-delay: 0.1s;
  }

  .content {
    color: white;
    width: 100%;
    height: 100%;
    transform: translateY(100%);
    box-sizing: border-box;
    padding: 30px;
  }

  .content h2 {
    font-size: 26px;
    line-height: 26px;
    margin-bottom: 20px;
  }

  .content h3 {
    font-size: 20px;
    line-height: 26px;
    margin-bottom: 5px;
  }

  .content h3 i {
    margin-right: 5px;
  }

  .content h4 {
    font-size: 14px;
    line-height: 20px;
  }

  .content h4 i {
    margin-right: 5px;
  }
</style>



