<template>
    <div>
        <!-- home -->
        <div :id="a.id" :class="a.class" :key="index" v-for="(a,index) in tab2Data">
            <div class="content">
                <h2>{{a.tit}}</h2>
                <p>{{a.content}}</p>
            </div>
        </div>

        <!-- Header with Navigation -->
        <div id="header">
            <ul id="navigation">
                <li :key="index" v-for="(b,index) in tab2Name"><a :id="b.id" :href="b.href">{{b.con}}</a></li>
            </ul>
        </div>
    </div>
</template>

<script>
    export default {
        name: "PcTabContor2",
        props:["tab2Data","tab2Name"]
    }
</script>

<style scoped>
    /* Top Bar Style */
    .codrops-top{
        line-height: 24px;
        font-size: 11px;
        background: rgba(255, 255, 255, 1);
        text-transform: uppercase;
        z-index: 9999;
        position: fixed;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 24px;
        box-shadow: 1px 0px 2px rgba(0,0,0,0.2);
        -webkit-animation: slideOut 0.5s ease-in-out 0.3s backwards;
        -moz-animation: slideOut 0.5s ease-in-out 0.3s backwards;
        -o-animation: slideOut 0.5s ease-in-out 0.3s backwards;
        -ms-animation: slideOut 0.5s ease-in-out 0.3s backwards;
        animation: slideOut 0.5s ease-in-out 0.3s backwards;
    }
    .codrops-top a{
        padding: 0px 10px;
        letter-spacing: 1px;
        color: #333;
        text-shadow: 0px 1px 1px #fff;
        display: block;
        float: left;
    }
    .codrops-top a:hover{
        background: #fff;
    }
    .codrops-top span.right{
        float: right;
    }
    .codrops-top span.right a{
        float: left;
        display: block;
    }
    /* Demo Buttons */
    #codrops-demos{
        position: fixed;
        z-index: 2000;
        top: 50px;
        right: 50px;
    }
    #codrops-demos a{
        display: inline-block;
        font-size: 12px;
        width: 60px;
        height: 24px;
        color: #000;
        line-height: 24px;
        text-align: center;
        box-shadow: 1px 1px 2px rgba(0,0,0,0.2);
        background: rgba(255,255,255,0.9);
        text-transform: uppercase;
    }
    #codrops-demos a:hover{
        background: #ddd;
        color: #000;
    }
    #codrops-demos a.current-demo,
    #codrops-demos a.current-demo:hover{
        background: #000;
        color: #fff;
        cursor: default;
    }
    @-webkit-keyframes slideOut{
        0%{top:-30px; opacity: 0;}
        100%{top:0px; opacity: 1;}
    }
    @-moz-keyframes slideOut{
        0%{top:-30px; opacity: 0;}
        100%{top:0px; opacity: 1;}
    }
    @-o-keyframes slideOut{
        0%{top:-30px; opacity: 0;}
        100%{top:0px; opacity: 1;}
    }
    @-ms-keyframes slideOut{
        0%{top:-30px; opacity: 0;}
        100%{top:0px; opacity: 1;}
    }
    @keyframes slideOut{
        0%{top:-30px; opacity: 0;}
        100%{top:0px; opacity: 1;}
    }
    html, body {
        height:100%;
    }
    body {
        width: 100%;
        background: #87cfe6;
        overflow: hidden;
    }
    #header{
        position: absolute;
        z-index: 2000;
        width: 235px;
        top: 50px;
    }
    #header h1{
        font-size: 30px;
        font-weight: 400;
        text-transform: uppercase;
        color: rgba(255,255,255,0.9);
        text-shadow: 0px 1px 1px rgba(0,0,0,0.3);
        padding: 20px;
        background: #000;
    }
    #navigation {
        margin-top: 20px;
        width: 235px;
        display:block;
        list-style:none;
        z-index:3;
    }
    #navigation a{
        color: #444;
        display: block;
        background: #fff;
        background: rgba(255,255,255,0.9);
        line-height: 50px;
        padding: 0px 20px;
        text-transform: uppercase;
        margin-bottom: 6px;
        box-shadow: 1px 1px 2px rgba(0,0,0,0.2);
        font-size: 14px;
    }
    #navigation a:hover {
        background: #ddd;
    }
    .content{
        right: 40px;
        left: 280px;
        top: 0px;
        position: absolute;
        padding-bottom: 30px;
    }
    .content h2{
        font-size: 110px;
        padding: 10px 0px 20px 0px;
        margin-top: 52px;
        color: #fff;
        color: rgba(255,255,255,0.9);
        text-shadow: 0px 1px 1px rgba(0,0,0,0.3);
    }
    .content p{
        font-size: 18px;
        padding: 10px;
        line-height: 24px;
        color: #fff;
        display: inline-block;
        background: black;
        padding: 10px;
        margin: 3px 0px;
    }
    .panel{
        min-width: 100%;
        height: 100%;
        overflow-y: auto;
        overflow-x: hidden;
        position: absolute;
        margin-top: -150%;
        background-color: #87cfe6;
        box-shadow: 0px 4px 7px rgba(0,0,0,0.6);
        opacity: 0;
        z-index:2;
        -webkit-transition: opacity .6s ease-in-out;
        -moz-transition: opacity .6s ease-in-out;
        -o-transition: opacity .6s ease-in-out;
        -ms-transition: opacity .6s ease-in-out;
        transition: opacity .6s ease-in-out;
    }
    .panel:target{
        opacity: 1;
        margin-top: 0%;
    }
    #home:target ~ #header #navigation #link-home,
    #portfolio:target ~ #header #navigation #link-portfolio,
    #about:target ~ #header #navigation #link-about,
    #contact:target ~ #header #navigation #link-contact{
        background: #000;
        color: #fff;
    }
    #works {
        padding: 15px 0px;
    }
    #works li{
        display: inline;
    }
    #works li a{
        padding-right: 10px;
    }
    #works img {
        box-shadow: 1px 1px 2px rgba(0,0,0,0.3);
        padding: 12px;
        background: rgba(255,255,255,0.9);
    }
    #form p{
        background: #fff;
        color: #000;
    }
    #form input, #form textarea{
        background: #000;
        color: #fff;
        border: none;
    }
    #form textarea{
        width: 493px;
        height: 100px;
    }
    #form label{
        padding-right: 10px;
        float: left;
    }
    .footnote a{
        color: #ddd;
    }
</style>


