<template>
  <div class="wrapper">
    <input type="radio" id="star1" name="star"/>
    <label for="star1" :style="{color:color}"></label>
    <input type="radio" id="star2" name="star"/>
    <label for="star2" :style="{color:color}"></label>
    <input type="radio" id="star3" name="star" checked="checked"/>
    <label for="star3" :style="{color:color}"></label>
    <input type="radio" id="star4" name="star"/>
    <label for="star4" :style="{color:color}"></label>
    <input type="radio" id="star5" name="star"/>
    <label for="star5" :style="{color:color}"></label>
  </div>
</template>

<script>
  export default {
    props: ["color"],
    name: "PcGrade1"
  }
</script>

<style scoped>
  input {
    display: none;
  }

  label {
    cursor: pointer;
    font-size: 40px;
    color: yellow;
    display: block;
    width: 50px;
    height: 50px;
    line-height: 60px;
    text-align: center;
    float: left;
    -moz-transition: all 0.2s;
    -o-transition: all 0.2s;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    text-shadow: 0 3px 3px #2b3c4e;
  }

  label:hover {
    font-size: 50px;
    color: #FFF;
    text-shadow: 0 0 5px #b9c9d8;
  }

  label:before {
    display: inline;
    width: auto;
    height: auto;
    line-height: normal;
    vertical-align: baseline;
    margin-top: 0;
    font-family: FontAwesome;
    font-weight: normal;
    font-style: normal;
    text-decoration: inherit;
    -webkit-font-smoothing: antialiased;
    content: "\f005";
  }

  input:checked + label ~ label:before {
    content: "\f006";
  }

  .wrapper {
    display: inline-block;
  }

  .wrapper:hover label:before {
    content: "\f005";
  }

  .wrapper:hover label:hover ~ label:before {
    content: "\f006";
  }
</style>



