import Vue from 'vue'
import App from './App'
import router from './router'
import Public from "./assets/css/public.css"
import Fonts from "./assets/css/iconfont.css";
import Clipboard from 'clipboard';

// highlight.js代码高亮指令
import Highlight from './assets/js/highlight';

Vue.use(Highlight);
/* eslint-disable no-new */
import ardorui from "./package/index";

Vue.use(ardorui);


new Vue({
  el: '#app',
  router,
  render(h) {
    return h(App);
  }
});



