<template>
  <ul class="menu">
    <li><a href="#" class="active">首页</a></li>
    <li><a href="#">团队</a></li>
    <li><a href="#">项目</a></li>
    <li><a href="#">关于我们</a></li>
    <li class="slider"></li>
  </ul>
</template>

<script>
  export default {
    name: "PcUiframe2"
  }
</script>

<style scoped>
  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  h3 {
    text-align: center;
    font-size: 24px;
    text-transform: uppercase;
    letter-spacing: 3px;
    color: #222;
  }

  .menu {
    list-style: none;
    padding: 0;
    width: 100%;
    height: 120px;
    margin: 0 auto;
    position: relative;
    background-color: #2c3e50;
    z-index: 7;
  }

  .menu li {
    float: left;
    width: 25%;
    height: 100%;
    margin: 0;
    padding: 0;
  }

  .menu a {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    color: #fff;
    text-decoration: none;
    position: relative;
    font-size: 18px;
    z-index: 9;
  }

  a.active {
    background-color: #e74c3c;
    pointer-events: none;
  }

  li.slider {
    width: 25%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background-color: #e74c3c;
    z-index: 8;
    transition: left 0.4s, background-color 0.4s;
  }

  .menu li:nth-child(1):hover ~ .slider,
  .menu li:nth-child(1):focus ~ .slider,
  .menu li:nth-child(1):active ~ .slider {
    left: 0;
    background-color: #3498db;
  }

  .menu li:nth-child(2):hover ~ .slider,
  .menu li:nth-child(2):focus ~ .slider,
  .menu li:nth-child(2):active ~ .slider {
    left: 25%;
    background-color: #9b59b6;
  }

  .menu li:nth-child(3):hover ~ .slider,
  .menu li:nth-child(3):focus ~ .slider,
  .menu li:nth-child(3):active ~ .slider {
    left: 50%;
    background-color: #e67e22;
  }

  .menu li:nth-child(4):hover ~ .slider,
  .menu li:nth-child(4):focus ~ .slider,
  .menu li:nth-child(4):active ~ .slider {
    left: 75%;
    background-color: #16a085;
  }

</style>



