<template>
  <div class="z-components-nav">
    <router-link tag="div" to="/" class="z-components-header">
      <!-- <img src="static/img/1552719765_761343.png" alt=""/> -->
      <h1 >开源UI</h1>
    </router-link>
    <div class="seek">
      <div class="z-components-seek">
        <input type="text" class="z-components-seek-inp" placeholder="找到你的梦想"/>
        <button type="button"></button>
      </div>
    </div>
    <components-menu :data="data"></components-menu>
  </div>
</template>

<script>
  import ComponentsMenu from "./ComponentsMenu";

  export default {
    props: ["data"],
    name: "ComponentsNav",
    components: {ComponentsMenu}
  }
</script>

<style scoped>
  .z-components-nav {
    width: 350px;
    background: rgba(0, 0, 0, .4);
    box-shadow: 0 1px 20px #2b2824;
    box-sizing: border-box;
    padding: 50px 20px 0 0;
    display: flex;
    flex-direction: column;
  }

  .z-components-header {
    overflow: hidden;
    margin-bottom: 50px;
    cursor: pointer;
    padding-left: 20px;
  }

  .z-components-header img {
    width: 50px;
    height: 50px;
    float: left;
  }

  .z-components-header h1 {
    font-size: 50px;
    line-height: 50px;
    float: left;
    font-weight: bold;
    margin-left: 20px;
     color: #fff;
     /* color:black; */
    text-shadow: 1px 1px 4px #fff;
  }

  .seek {
    padding-left: 20px;
  }

  .z-components-seek {
    width: 100%;
    height: 40px;
    border-radius: 50px;
    overflow: hidden;
    background: rgba(204, 204, 204, .4);
    margin-bottom: 50px;
    box-sizing: border-box;
  }

  .z-components-seek input {
    width: 80%;
    height: 100%;
    border: none;
    float: left;
    font-size: 20px;
    text-indent: 20px;
    background: none;
    color: #ccc;
  }

  .z-components-seek button {
    width: 20%;
    height: 100%;
    float: left;
    border: none;
    outline: none;
    cursor: pointer;
    background: none;
  }
</style>



