<template>
    <div>
        <table id="tables">
            <!-- 加载进度 -->
            <div id="progress"></div>
            <tr :key="index" v-for="(a,index) in tableData">
                <td :key="index" v-for="(b,index) in a">{{b}}</td>
            </tr>
        </table>
        <div class="w-table-pages">
            <h1 class="PreviousPage">上一页</h1>
            <p class="curr">1</p>
            <p>2</p>
            <p>3</p>
            <p>4</p>
            <p>5</p>
            <h1 class="NextPage">下一页</h1>
        </div>
    </div>
</template>

<script>
    export default {
        name: "PcTables",
        props:["tableData"],
        mounted(){
            // 表格加载进度条
            let num = 0;
            let TotalWidth = document.getElementById("tables").offsetWidth
            let progress = document.getElementById("progress")
            setInterval(() => {
                num += 25;
                if (num >= TotalWidth) {
                    num = 0;
                }
                progress.style.left += 500
                // console.log(progress.offsetLeft)
            }, 1000);
        }
    }
</script>

<style scoped>
    /* 表格 */
    table{
        width: 100%;
        border-collapse: collapse;
        border-spacing: 10px;
        table-layout: fixed;
        text-align: center;
        color: #d1cdca;
        position: relative;
        overflow: hidden;
    }
    /* 加载进度 */
    #progress {
        width: 50%;
        height: 3px;
        top: 69px;
        left: 0;
        background: #2196f3;
        position: absolute;
    }
    /* 每一行的底边框 */
    table tr{
        height: 70px;
        border-bottom: 1px solid black;
    }
    table tr:nth-of-type(1){
        border-bottom: 3px solid black;
    }
    table tr td{
        text-align: center;
    }
    input[type=button]{
        width: 70%;
        height: 50px;
        border-radius: 20px;
        cursor: pointer;
        border: 2px solid black;
    }
    /* 页码 */
    .w-table-pages{
        margin-left: 67%;
    }
    h1,p{
        float: left;
        font-size: 20px;
        margin-top: 30px;
        margin-right: 30px;
        margin-bottom: 20px;
        cursor: pointer;
    }
    .curr{
        color: #f46b89;
    }
</style>


