<template>
  <div class="helloWord">
    <p class="rainbow">
      {{val}}
      <span :data-text="val"></span>
      <span :data-text="val"></span>
      <span :data-text="val"></span>
      <span :data-text="val"></span>
    </p>
  </div>
</template>

<script>
  export default {
    props: ["val"],
    name: "helloWord"
  }
</script>

<style scoped>
  .helloWord {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .rainbow {
    color: transparent;
    font-size: 100px;
    text-transform: uppercase;
    font-family: sans-serif;
    font-weight: bold;
    line-height: 1em;
    position: relative;
  }

  .rainbow span::before,
  .rainbow span::after {
    content: attr(data-text);
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    animation: animate 0.8s infinite alternate;
    filter: opacity(0);
    z-index: var(--n);
    height: calc(100% - 10% * var(--n));
    animation-delay: calc(0.8s - 0.1s * var(--n));
  }

  .rainbow span:nth-child(1)::before {
    color: orchid;
    --n: 1;
  }

  .rainbow span:nth-child(1)::after {
    color: mediumpurple;
    --n: 2;
  }

  .rainbow span:nth-child(2)::before {
    color: deepskyblue;
    --n: 3;
  }

  .rainbow span:nth-child(2)::after {
    color: cyan;
    --n: 4;
  }

  .rainbow span:nth-child(3)::before {
    color: mediumspringgreen;
    --n: 5;
  }

  .rainbow span:nth-child(3)::after {
    color: yellow;
    --n: 6;
  }

  .rainbow span:nth-child(4)::before {
    color: gold;
    --n: 7;
  }

  .rainbow span:nth-child(4)::after {
    color: tomato;
    --n: 8;
  }

  @keyframes animate {
    from {
      filter: opacity(0);
    }

    to {
      filter: opacity(1);
    }
  }
</style>



