<template>
    <div>
        <div id="count">
            <span>1</span>,
            <span>2</span>
            <span>3</span>
            <span>4</span>,
            <span>5</span>
            <span>6</span>
            <span>7</span>,
            <span>8</span>
            <span>9</span>
            <span>10</span>
        </div>
    </div>
</template>

<script>
    export default {
        name: "PcNumTotal3",
        mounted() {
            // 注册人数
            let num = 3141594325;
            setInterval(function(){
                let random = Math.round(Math.random()*40+10)	/*随机范围1-50*/
                num+= random;
                let str = num.toString();
                for(let i in str){
                    let positionY = "";
                    switch(str[i]){
                        case "0": positionY="0";break;
                        case "1": positionY="-45";break;
                        case "2": positionY="-90";break;
                        case "3": positionY="-135";break;
                        case "4": positionY="-180";break;
                        case "5": positionY="-225";break;
                        case "6": positionY="-270";break;
                        case "7": positionY="-315";break;
                        case "8": positionY="-360";break;
                        case "9": positionY="-405";break;
                    }
                    document.getElementsByTagName("span")[i].style.backgroundPositionY = positionY
                }
            },3000)
        }
    }
</script>

<style scoped>
    /* 注册人数 */
    #count {
        float: left;
        margin-top: 15px;
        color:#d1cdca;
        font-size: 20px;
    }
    #count span{
        font-size: 25px;
        color: rgba(0,0,0,0);
        text-align: center;
        display: inline-block;
        width: 37px;
        height: 37px;
        background: url(../../../../static/img/count/vipCount3.png);
    }
    #count span:nth-child(1),#count span:nth-child(4),#count span:nth-child(7){
        margin-right: 15px;
    }
    #count span:nth-child(1){
        background-position: 0px -135px;
    }
    #count span:nth-child(2){
        background-position: 0px -45px;
    }
    #count span:nth-child(3){
        background-position: 0px -180px;
    }
    #count span:nth-child(4){
        background-position: 0px -45px;
    }
    #count span:nth-child(5){
        background-position: 0px -225px;
    }
    #count span:nth-child(6){
        background-position: 0px -405px;
    }
    #count span:nth-child(7){
        background-position: 0px -180px;
    }
    #count span:nth-child(8){
        background-position: 0px -135px;
    }
    #count span:nth-child(9){
        background-position: 0px -90px;
    }
    #count span:nth-child(10) {
        background-position: 0px -225px;
    }
</style>


